
import { Ad } from "@/store/state";
import { defineComponent, PropType } from "vue";

export default defineComponent({
  name: "PopupAd",
  props: {
    ad: {
      type: Object as PropType<Ad>,
      required: true,
    },
  },
  emits: ["close"],
  mounted() {
    document.addEventListener(
      "keydown",
      (e) => e.key == "Escape" && this.$emit("close"),
    );
  },
});
